import {
    Notification
} from 'element-ui'
// 
// import 'element-ui/lib/theme-chalk/index.css'

export default {
    handleSuccess: function (msg) {
        Notification({
            title: msg || '',
            type: 'success'
        });
    },
    handleWarning: function (msg) {
        Notification({
            title: msg || '',
            type: 'warning'
        });
    },
    handleError: function (msg) {
        Notification({
            title: msg || '',
            type: 'error'
        });
    },
}