var qs = require('qs');
import axios from 'axios';

import NProgress from 'nprogress'
import 'nprogress/nprogress.css'

import router from "@/router";
import utils from '@/static/js/common/utils'
import Loading from '@/components/loading'

axios.defaults.timeout = 1 * 30 * 1000;

if (process.env.NODE_ENV === 'production') {
  axios.defaults.baseURL = process.env.VUE_APP_URL
}

axios.defaults.withCredentials = true
axios.interceptors.request.use(
  config => {
    NProgress.start()
    Loading.start()
    config.headers = {
      'Content-Type': 'application/x-www-form-urlencoded'
    }
    return config;
  },
  error => {
    NProgress.done()
    Loading.done()
    return Promise.reject(err);
  }
);

axios.interceptors.response.use(
  response => {
    NProgress.done()
    Loading.done()
    if (response.status == 401) {
      router.push({
        path: "/login",
        query: {
          redirect: router.currentRoute.fullPath
        } // 
      })
    }
    return response;
  },
  error => {
    NProgress.done()
    Loading.done()
    return Promise.reject(error)
  }
);

export default {
  fetch: function (url, params = {}, options = {}) {
    let {
      successNotify = false, errorNotify = true
    } = options;

    /*
         http status  2xx   then  err 
         then  err  catch 
    */
    return new Promise((resolve, reject) => {
      axios.get(url, {
        params: params
      }).then(response => {
        // http status  2xx
        if (response.data.code == 1) {
          // api 
          if (successNotify) utils.handleSuccess(response.data.msg);
        } else {
          // api 
          if (errorNotify) utils.handleWarning(response.data.msg);
        }
        //  service 
        resolve(response.data);
      }, err => {
        //  2xx 
        switch (err.response.status) {
          case 401:
            utils.handleError('')
            router.push("/login");
            break;
          default:
            utils.handleError('')
        }
        reject(err)
      }).catch(err => {
        //  then  err 
        utils.handleError(',');
        reject(err)
      })
    })
  },

  post: function (url, data = {}, options = {}) {
    let {
      successNotify = true, errorNotify = true
    } = options;

    /*
         http status  2xx   then  err 
         then  err  catch 
    */
    return new Promise((resolve, reject) => {
      axios.post(url, qs.stringify(data))
        .then(response => {
          // http status  2xx
          if (response.data.code == 1) {
            // api 
            if (successNotify) utils.handleSuccess(response.data.msg);
          } else {
            // api 
            if (errorNotify) utils.handleWarning(response.data.msg);
          }
          resolve(response.data);
        }, err => {
          //  2xx 
          switch (err.response.status) {
            case 401:
              utils.handleError('')
              router.push("/login");
              break;
            default:
              utils.handleError('')
          }
          reject(err)
        }).catch(err => {
          //  then  err 
          utils.handleError(',');
          reject(err)
        })
    })
  },
}